import common from '../../common/common.js'

export default {
	data() {
		return {
			integralList:[],
			isall:0,
			page:0,
			type:'wallet',
			current:0,			//转出类型 0:不可提现账户 1:可提现账户
			amounts:0,			//转出积分
			popzhuan:false,		
			pingjun:0,			//平均每天单数
			needpingjum:10,		//需每天平均单数
			userInfo:[],
			
			money:0,
			duihuanbi:100,		//积分兑换比
			
			tabindex:0,			
		}
	},
	onLoad() {
		var that = this
		var userInfo = uni.getStorageSync('userInfo');
		var utype =  uni.getStorageSync('utype');			//用户类型
		
		that.userInfo = userInfo
		that.needpingjum = userInfo.shop_pingjundan
		
		
		that.getIntegralList();
	},
	onPullDownRefresh() {
		var that = this
		uni.showNavigationBarLoading();
		console.log('下拉刷新中...');
		that.resetAttr();

		if(that.tabindex == 1){
			that.getIntegralTempList();
		}else{
			that.getIntegralList();
		}
		that.getUserInfo();
		
		uni.hideNavigationBarLoading();
		uni.stopPullDownRefresh();
	},
	onReachBottom() {
		var that = this
		uni.showNavigationBarLoading();	
		
		that.getIntegralList();
		
		uni.hideNavigationBarLoading();
		uni.stopPullDownRefresh();
	},
	methods: {
		switchTabs(e){
			var that = this
			var tabindex = e.currentTarget.dataset.tabindex
			console.log(tabindex)
			if(tabindex != that.tabindex){
				that.tabindex = tabindex
				that.resetAttr();
				if(tabindex == 0){
					that.getIntegralList();
				}else{
					that.getIntegralTempList();
				}
			}
		},
		toggleZhuan(){
			var that = this
			
			if(!that.popzhuan){
				var account = that.userInfo.users_integral
				if(account == 0){
					uni.showToast({
						title:'积分不足',
						icon:'none',
						duration:1500
					})
					return false
				}
			}
			that.popzhuan = !that.popzhuan
		},
		checkAmounts(e){
			var that = this
			var amounts = e.detail.value
			//var users_integral = that.userInfo.users_integral
			//amounts = amounts > users_integral ? users_integral : amounts
			that.amounts = amounts
			that.recountMoney();
		},
		setall(){
			var that = this
			var integral = that.userInfo.users_integral
			that.amounts = integral
			that.recountMoney();
		},
		//计算可得余额
		recountMoney(){
			var that = this
			var duihuanbi  = that.duihuanbi
			var money = (that.amounts/duihuanbi).toFixed(2);
			that.money = money
		},
		submitSave(e){
			var that = this
			var userInfo = uni.getStorageSync('userInfo');
			var integral = userInfo.users_integral
			var amounts = parseFloat(that.amounts)
			var weixin = e.detail.weixin
			
			if(amounts == 0){
				uni.showToast({
					title:'请输入提现金额',
					icon:'none',
					duration:1500
				})
				return false
			}
			
			if(amounts > integral){
				uni.showToast({
					title:'积分不足',
					icon:'none',
					duration:1500
				})
				return false
			}
			
			uni.request({
				url: common.apiUrl + 'user/saveDuihuan',
				method: 'GET',
				data: {
					userid:userInfo.users_id,
					amount:amounts
				},
				success: res => {
					if(res.data.code == 0){
						uni.showToast({
							title:'操作成功',
							icon:'success',
							duration:1500,
							success:res=>{
								setTimeout(function(){
									that.resetAttr();
									that.getIntegralList();
									that.toggleZhuan();
									that.getUserInfo()
								})
							}
						})
					}else{
						uni.showToast({
							title:res.data.msg,
							icon:'none',
							duration:1500									
						})
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		radioChange: function(evt) {
			var that = this
			that.current = evt.target.value;
			console.log(that.current);
		},
		//切换账户状态
		changeAccountType(e){
			var that = this
			var t_type = e.currentTarget.dataset.type
			if(t_type == that.type){
				return false
			}else{
				that.type = t_type
				that.resetAttr();
				that.getData();
			}
		},
		resetAttr(){
			var that = this
			that.page = 0
			that.isall = 0
			that.integralList = []
		},
		getData(){
			var that = this
			that.getIntegralList();
		},
		
		//获取用户信息
		getUserInfo(){
			var that = this
			var userInfo = uni.getStorageSync('userInfo');
			var userid = userInfo.users_id
			if(userid > 0){
				uni.request({
					url: common.apiUrl+'user/getUserInfo',
					method: 'GET',
					data: {
						userid:userid
					},
					data: {
						userid:userid
					},
					success: res => {
						if(res.data.code == 0){
							var userInfo = res.data.userInfo;
							uni.setStorageSync('userInfo',userInfo);
							that.userInfo = userInfo
						}
					},
					fail: () => {},
					complete: () => {}
				});
			}else{
				uni.showModal({
					title: '提示',
					content: '你还没登录，现在去登录吗？',
					showCancel: true,
					cancelText: '取消',
					confirmText: '确定',
					success: res => {
						if(res.confirm){
							uni.navigateTo({
								url: '/pages/userlogin/userlogin',
								success: res => {},
								fail: () => {},
								complete: () => {}
							});
						}
					},
					fail: () => {},
					complete: () => {}
				});
			}
		},
				
		//获取积分明细列表
		getIntegralList(){
			var that = this
			var isall = that.isall
			console.log('isall',isall)
			if(isall == 1){
				return false;
			}
			var userInfo = uni.getStorageSync('userInfo');
			var uid = userInfo.mem_id
			uni.request({
				url: common.apiUrl + 'user/getIntegralList',
				method: 'GET',
				data: {
					uid:userInfo.users_id,
					page:that.page+1
				},
				success: res => {
					that.integralList = that.integralList.concat(res.data.integral_list);
					that.isall = res.data.isall;
					console.log(res.data.integral_list);
					that.page = res.data.page
					console.log(that.page);
				},
				fail: () => {},
				complete: () => {}
			});
		},
		
		//获取积分临时表明细列表
		getIntegralTempList(){
			var that = this
			var isall = that.isall
			console.log('isall',isall)
			if(isall == 1){
				return false;
			}
			var userInfo = uni.getStorageSync('userInfo');
			var uid = userInfo.mem_id
			uni.request({
				url: common.apiUrl + 'user/getIntegralTempList',
				method: 'GET',
				data: {
					uid:userInfo.users_id,
					page:that.page+1
				},
				success: res => {
					that.integralList = that.integralList.concat(res.data.integral_list);
					that.isall = res.data.isall;
					console.log(res.data.integral_list);
					that.page = res.data.page
					console.log(that.page);
				},
				fail: () => {},
				complete: () => {}
			});
		}
		
	}
}
